body {
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: #4c4c4c;
  background-color: #fcfcfc;
}

code,
kbd,
pre,
samp {
  font-family: "Source Coe Pro", SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", monospace;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-weight: 700;
  color: #2f2f2f;
}

.bd-navbar {
  min-height: 4rem;
  padding: .43rem 1rem;
  background: #0f9c8d;
  /* Old browsers */
  background: linear-gradient(to right, #0f9c8d 15.57%, #11b5a4 83.9%);
  box-shadow: none;
}

@media (max-width: 991.98px) {
  .bd-navbar {
    padding-right: .5rem;
    padding-left: .5rem;
  }
  .bd-navbar .navbar-nav-scroll {
    max-width: 100%;
    height: 3.5rem;
    margin-top: 0;
    overflow: hidden;
    font-size: .875rem;
  }
  .bd-navbar .navbar-nav-scroll .navbar-nav {
    padding-top: .6rem;
    padding-bottom: 2rem;
    overflow-x: auto;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
  }
}

@media (min-width: 768px) {
  @supports ((position: -webkit-sticky) or (position: sticky)) {
    .bd-navbar {
      position: -webkit-sticky;
      position: sticky;
      top: 0;
      z-index: 1071;
    }
  }
}

.bd-navbar .navbar-brand span {
  font-size: 18px;
  font-weight: 700;
  color: #fff;
  opacity: .4;
}

.bd-navbar .navbar-nav .nav-link {
  position: relative;
  padding-right: 1rem;
  padding-left: 1rem;
  font-size: .75rem;
  font-weight: 600;
  color: #fff;
  background-color: transparent;
  transition: background-color .1s linear, color .1s linear;
}

.bd-navbar .navbar-nav .nav-link::after {
